<template>
    <api-request :resource="getData" v-model="data">
        <h3>Datos Ficticios de pago</h3>
        <pre v-if="data">{{data|pretty}}</pre>
        <div>{{id}}</div>
        <b-btn to="/APP/Khipu/Pago?type=init">pagar</b-btn>
    </api-request>
</template>



<script>
/* El componente recibe los datos para hacer la transacción.
   Se manda a generar la transacción con los datos y medio de pago.
   se reciben los datos, se publican y se muestran los medios de pago.
   al pinchar el medio de pago se debe generar la TRX interna y devolver las opciones de URL
*/
export default {
    props:{
        pago:{
            type:Object,
            //required:true,
            validate(obj){
                return obj.subjet && obj.amount && obj.transaction_id
            }
        },
        id:{
            type:String
        }
    },
    routes:[
        {
            path:"test",
            component:Vue.component("Test")
        }
    ],
    data(){
        console.log(this.$route);
        console.log(this.$router);
        return {
            data:{}
        }
    },
    methods:{
        getData(){
            return this.$http.get(this.$route.path+".json").then(({body})=>body);
        }
    },
    filters:{
        pretty(val){
            return JSON.stringify(val,null,2);
        },
    }
}
</script>




